@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,700;1,400;1,700&display=swap);
.navbar{max-height:80px;margin-bottom:20px;background:var(--theme-header-color);box-shadow:0px 0px 10px 1px #000}.navbar-brand-img{width:100px;height:100px;vertical-align:top}@media screen and (max-width: 601px){.navbar-brand{padding:0;margin:0}.navbar-brand-img{width:70px;height:70px}}.navbar .filters{display:flex;width:78%;transition:all ease-in-out .5s}@media screen and (max-width: 1280px){.navbar .filters{width:91%}}@media screen and (max-width: 1201px){.navbar .filters{display:flex;position:absolute;width:30%;background:var(--theme-background-color);top:0;right:-307px;height:100vh;flex-direction:column;justify-content:space-evenly;align-items:center}.navbar .filters .nav-pills li{text-align:center;width:100%;margin-bottom:.5rem}.navbar .filters .form-inline{margin:0 !important}.navbar .filters .close_icon{display:block;position:absolute;top:25px;right:25px;background:rgba(0,0,0,0);border:none}.navbar .filters .close_icon i{font-size:1.8rem;color:var(--theme-font-color)}.navbar .filters .mr-auto{margin-right:0 !important}}@media screen and (max-width: 601px){.navbar .filters{width:50%}}@media screen and (max-width: 360px){.navbar .filters{width:75%}}.navbar .menu{color:var(--theme-font-color);font-size:1.2rem;transition:all ease-in .3s;border-bottom:2px solid rgba(0,0,0,0)}.navbar .menu:focus,.navbar .menu:hover{box-shadow:none;border-bottom:2px solid var(--theme-font-color);border-radius:0}.navbar .search_input{outline:none;height:40px;border-radius:15px;border:none;padding:10px;font-size:20px}.navbar .change_theme:focus{box-shadow:none}.navbar .change_theme i{font-size:1.7rem}.navbar .change_theme i.fa-sun,.navbar .change_theme i.fa-magnifying-glass{color:var(--theme-font-color)}.navbar .change_theme i.fa-moon{color:var(--theme-font-color)}.navbar .close_icon{display:none}@media screen and (max-width: 1201px){.navbar .navbar-toggler{display:block !important}}::-webkit-input-placeholder{color:#140d15}:-ms-input-placeholder{color:#140d15}::placeholder{color:#140d15}
*{margin:0;padding:0;box-sizing:border-box}body{background:var(--theme-background-color) !important;font-size:17px;transition:all ease-in-out .4s}body.dark{--theme-font-color: #eeeeee;--theme-font-color-secondary: #ffd369;--theme-background-color: #222831;--theme-header-color: #222831}body.ligth{--theme-font-color: #2e2d4d;--theme-font-color-secondary: #f4b942;--theme-background-color: #c7ebf0;--theme-header-color: #c7ebf0}p,h1,h2,h3,h4,h5,h6{color:var(--theme-font-color) !important}::-webkit-scrollbar{width:0px}
.container-cards{display:flex;flex-wrap:wrap}.container-cards .card_champ{position:relative;margin:10px 1.09rem;transition:all .3s;cursor:pointer;max-width:154px;border-radius:8px;overflow:hidden}@media screen and (max-width: 1280px){.container-cards .card_champ{max-width:143px}}@media screen and (max-width: 1024px){.container-cards .card_champ{max-width:130px}}@media screen and (max-width: 601px){.container-cards .card_champ{max-width:155px}}@media screen and (max-width: 360px){.container-cards .card_champ{max-width:130px}}.container-cards .card_champ__image{width:100%;height:auto;border-radius:8px;transition:all .7s;-webkit-transform:scale(1.2);transform:scale(1.2)}.container-cards .card_champ__info{transition:all .9s;display:none;justify-content:center}.container-cards .card_champ__info-name{position:absolute;text-align:center;font-size:1.3rem;color:#fff !important;font-weight:bold;top:10%}.container-cards .card_champ__info-title{position:absolute;bottom:40%;font-size:1rem;color:#fff !important;text-align:center;margin:0px 10px;bottom:46%}.container-cards .card_champ:hover .card_champ__info{display:flex}.container-cards .card_champ:hover .card_champ__image{-webkit-transform:scale(1);transform:scale(1);-webkit-filter:brightness(0.5);filter:brightness(0.5)}
.main{margin-top:100px;padding:0px 15px 0px 15px}
p{font-size:1rem}
.champ{margin-top:80px}.champ__header{display:flex;justify-content:space-between;padding:15px 10px;border-bottom:1px solid var(--theme-font-color);align-items:center}.champ__header-icon{text-decoration:none}.champ__header-icon i{font-size:2rem;color:var(--theme-font-color)}.champ__header-name{color:var(--theme-font-color);font-size:2.5rem;font-weight:bold}.champ__info{margin-top:50px;display:flex}.champ__info-image{width:60%;padding-left:40px}.champ__info-image img{width:100%;vertical-align:top}.champ__info-text{width:40%;display:flex;align-items:center;flex-wrap:wrap}.champ__info-text h2{text-align:center;font-size:1.5rem;color:var(--theme-font-color);text-transform:uppercase;width:100%}.champ__info-text p{font-size:1.3rem;color:var(--theme-font-color);line-height:30px;text-align:justify}
